// 获取URL中的参数
export function getQuery(name, url) {
    var u = url || window.location.search,
        reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)'),
        r = u.substr(u.indexOf('\?') + 1).match(reg);
    return r != null ? r[2] : '';
}

//获取URL的最后一部分
export function getLastUrl(url) {
    url = url || window.location.href
    if(!url || typeof url !== 'string'){
        return ''
    }
    if(url.lastIndexOf('/') === url.length - 1){
        url = url.substring(0, url.length - 1)
    }
    return url.substring(url.lastIndexOf('/') + 1)
}
//读取COOKIE
export function getCookie(name) {
    var reg = new RegExp('(^| )' + name + '(?:=([^;]*))?(;|$)'),
        val = document.cookie.match(reg);
    return val ? (val[2] ? decodeURIComponent(val[2]) : '') : null;
}

//写入COOKIES
//默认情况下cookie是暂时存在的，他们存储的值只在浏览器会话期间存在，当用户退出浏览器后这些值也会丢失
export function setCookie(name, value, expires, path, domain, secure) {
    var exp = new Date();
    // use expires attribute, max-age is not supported by IE
    expires = expires || null; //expires不设置的时候，用户关闭浏览器值就会丢失
    path = path || '/';
    domain = domain || null;
    secure = secure || false;
    expires ? exp.setMinutes(exp.getMinutes() + parseInt(expires)) : '';
    document.cookie = name + '=' + encodeURIComponent(value) + (expires ? ';expires=' + exp.toGMTString() : '') + (path ? ';path=' + path : '') + (domain ? ';domain=' + domain : '') + (secure ? ';secure' : '');
}

// 删除COOKIES
export function delCookie(name) {
    setCookie(name, '', -1);
}

// debounce的作用是在让在用户动作停止后延迟x ms再执行回调。
// throttle的作用是在用户动作时每隔一定时间（如200ms）执行一次回调。
// debounce用在keydown/keypress事件上最好
// 而throttle用在resize改变布局上，onscroll滚动时候的
// 延迟执行
export function debounce(func, delay) {
    delay = delay || 200
    let timer

    return function (...args) {
        if (timer) {
            clearTimeout(timer)
        }
        timer = setTimeout(() => {
            func.apply(this, args)
        }, delay)
    }
}

// 每隔一段时间执行
export function throttle(func, delay) {
    let last
    let timer
    let interval = interval || 200

    // ...args将arguments对象转换为数组
    return function(...args){
        let now = +new Date()
        if(last && now - last < interval){
            clearTimeout(timer)
            timer = setTimeout(() => {
                last = now
                fn.apply(this, args)
            }, interval)
        } else {
            last = now
            fn.apply(this, args)
        }
    }
}

function getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min)
}

// 随机打乱数组
export function shuffle(arr) {
    let _arr = arr.slice()
    for (var i = 0; i < _arr.length; i++) {
        let j = getRandomInt(0, i)
        let t = _arr[i]
        _arr[i] = _arr[j]
        _arr[j] = t
    }
    return _arr
}

export function isEmpty(value){
    if(value == null || value === ''){
        return true
    }
    return false
}

// 判断是不是手机号码
export function isMobile(val){
    let patrn = /^0?1[2|3|4|5|7|8]\d{9}$/
    return patrn.test(val)
}


// 判断是不是座机
export function isZuoJi(val) {
    var patrn = /^(0\d{2,3}-?)?\d{7,8}$/;
    return patrn.test(val);
}
// 判断是不是HTTP/HTTPS请求
export function isHttp(url) {
    if(!url){
        return false
    }
    if (url.indexOf('http://') === 0 || url.indexOf('https://') === 0) {
        return true
    }
    return false
}

// 深拷贝数组
export function deepCopyArray(arr){
    return arr.map((e)=>{
        if(typeof e === 'object' && e !== null){
            return Object.assign({},e)
        } else if(e instanceof Array){
            return e.slice(0)
        } else {
            return e
        }
    })
}


