import http from './service'

// 修改密码
export function changePassword(data) {
    let url = '/api/ChangePassword'

    return http.put(url, data)
}

// 获取推荐的会员列表
export function getIRecommendList(data) {
    let url = '/api/MemberUser'

    return http.get(url, data)
}

// 查看查看商家信息
export function getCurrentMerchant(data) {
    let url = '/api/CurrentMerchant'

    return http.get(url, data)
}

// 查看查看推广二维码
export function getCurrentUser(data) {
    let url = '/api/CurrentUser'

    return http.get(url, data)
}


